<template>
<div>
    <div id="app">
    <div id="nav" :class="{active:isActive}">
      <select name="theme" class="theme" v-model="themeValue" @change="changeTheme">
        <option v-for='(item,index) in themeList' :Key='index' :value="item" >{{item}}</option>
      </select>
      <select name="language" class="language" v-model="languageValue" @change="changeLanguage">
          <option :value="item" v-for='(item,index) in languageList' :Key='index'>{{item}}</option>
      </select>
    </div>
    <router-view/>
  </div>
</div>
</template>
<script>
export default {
  name: 'HomeHeader',
  data () {
    return {
      themeValue: '红色',
      languageValue: '中文',
      themeList: ['红色', '蓝色'],
      languageList: ['中文', '英文'],
      isActive: false
    }
  },
  created () {
    this.optionsValue = this.themeList[0]
    this.languageValue = this.languageList[0]
  },
  methods: {
    changeTheme () {
      if (this.themeValue === '蓝色') {
        this.isActive = true
      } else {
        this.isActive = false
      }
      this.$emit('changeThemeinc', this.themeValue)
    },
    changeLanguage () {
      this.$emit('changeLanguageInc', this.languageValue)
    }
  },
  watch: {
  }
}
</script>
<style lang='less' scoped>
@media screen and (max-width:719px){
 #nav{
  display: flex;
  justify-content: flex-end;
  height: 50px;
  background: red;
  select{
    font-size: 16px;
    margin-right: 5px;
  }
  .language{
    margin-right: 30px;
  }
}
#nav.active{
  background: blue;
}
}
@media screen and (min-width:720px){
 #nav{
  display: flex;
  justify-content: flex-end;
  height: 25px;
  background: red;
  select{
    font-size: 8px;
    margin-right: 2.5px;
  }
  .language{
    margin-right: 15px;
  }
}
#nav.active{
  background: blue;
}
}

</style>
