<template>
<div class="wrapper">
    <div class="title">
        {{title}}
    </div>
    <div class="detail">
        {{detail}}
    </div>
    <div ctitlelass="buttom">
        <button :class="{active:isActive}">{{enter}}</button>
    </div>
</div>
</template>
<script>
export default {
  name: 'HomeContent',
  props: {
    curTheme: String,
    curLang: String
  },
  data () {
    return {
      title: '达蒙科技',
      detail: '让建筑更安全',
      isActive: false,
      enter: '进入官网'
    }
  },
  watch: {
    curTheme: function () {
      if (this.curTheme === '蓝色') {
        this.isActive = true
      } else {
        this.isActive = false
      }
    },
    curLang: function () {
      if (this.curLang === '英文') {
        this.title = 'Da technology'
        this.detail = 'Make the building safer'
        this.enter = 'Enter'
      } else {
        this.title = '达蒙科技'
        this.detail = '让建筑更安全'
        this.enter = '进入官网'
      }
    }
  }
}
</script>

<style lang='less' scoped>
@media screen and (max-width: 719px){
    .title{
      padding-top: 30vh;
      font-size: 50px;
      text-align: center;
  }
  .detail{
      padding-top: 20px;
      height: 100px;
      font-size: 20px;
      text-align: center;
  }
  button{
      display: block;
      width: 100px;
      text-align: center;
      margin: 0 auto;
      border:1px solid red;
      border-radius: 10px;
      font-size: 18px;
  }
  button.active{
      border:1px solid blue;
  }
  }
@media screen and (min-width: 720px){
    .title{
      padding-top: 30vh;
      font-size: 25px;
      text-align: center;
  }
  .detail{
      padding-top: 10px;
      height: 50px;
      font-size: 10px;
      text-align: center;
  }
  button{
      display: block;
      width: 100px;
      text-align: center;
      margin: 0 auto;
      border:1px solid red;
      border-radius: 5px;
      font-size: 9px;
  }
  button.active{
      border:1px solid blue;
  }
  }

</style>
