<template>
  <div class="home">
    <home-header @changeThemeinc='handleChange' @changeLanguageInc='handleChangeLa'></home-header>
    <home-content :curTheme='curTheme' :curLang='curLang'></home-content>
  </div>
</template>

<script>
// @ is an alias to /src
import HomeHeader from '../components/HomeHeader'
import HomeContent from '../components/HomeContent'
export default {
  name: 'home',
  data () {
    return {
      curTheme: '红色',
      curLang: '中文'
    }
  },
  components: {
    HomeHeader,
    HomeContent
  },
  methods: {
    handleChange (step) {
      this.curTheme = step
    },
    handleChangeLa (step) {
      this.curLang = step
    }
  }
}
</script>
